// SQ Calendar by Soomin K.
// Nov, 2018
// Selector name should be in camelCase instead of using hyphens:
// so that you can use style.ywToday instead of style.['yw-today'] in JS

// use rgba() to apply transparent color; to background or foreground

* {
  margin: 0;
  padding: 0;
}

// SASS Variables
$DecoLevel: 4;  // Decorative Level, not essential as DLv goes higher
// DLv 4: text-shadow in <td>
// DLv 3: text-shadow, linear-gradient
// DLv 2: box-shadow
// DLv 1: some background-color

$border-style: 1px solid Gray;   // groove
$week-number-color: DimGray;

$op-demo: 0;

@mixin textShadow($offx, $offy, $blur, $color, $alpha, $DL: 3) {
  @if ($DecoLevel >= $DL) {
    text-shadow: $offx $offy $blur rgba($color, $alpha);
  }
}

@mixin boxShadow($offx, $offy, $blur, $spread, $color, $alpha, $DL: 2) {
  @if ($DecoLevel >= $DL) {
    box-shadow: $offx $offy $blur $spread rgba($color, $alpha);
  }
}

@mixin bkgLinearGradient($color1, $color2, $alpha, $dir: to bottom, $DL: 2) {
  @if ($DecoLevel >= $DL) {
    background-image: linear-gradient($dir, $color1, rgba($color2, $alpha));
  }
}

