table.ywFullyear {
  table-layout: auto;
  width: 13em;            // added for the case where the context changes
  line-height: 100%;      // default 110%
  td {
    border: 0;            // added to initialize
    white-space: nowrap;
  }
  border-collapse: collapse;
  border-spacing: 2px 0;
  border-radius: 0 0 1rem 0;
  @include boxShadow(1px, 1px, 2px, 1px, Black, .2);
  margin: 5px;
  animation: slide-from-left 1s cubic-bezier(.5,.03,.64,1.65);
  transition: opacity 1s, transform 1s;
