  &:hover {
    opacity: 1;
    transform: translateX(0);
  }
*/
}

  .hideDrawer {
    transform: translateX(-105%);
    transition: transform 1s;
  }

@keyframes slide-from-left {
  0%   { transform: translateX(-100%); }
  100% { transform: translateX(0); }
}

@keyframes blink-background {
  50% { background-color: transparent; }
}

@keyframes blink-text {
  50% { color: transparent; }
}

@keyframes blink-border {
  50% { border: 1px solid transparent; }
}

//.ywToday > .ywDate {
// Tried to test if I can apply the pular directly to <td> date cell but failed.
// Couldn't center the pular on the date number when the width and height >= 1rem.
td #pulsar {
  position: absolute;
  width: 1.5em;
  height: 1.5em;

  &:before {
    content: '';
    position: relative;
    top: -80%;
    left: -80%;
    width: 250%;
    height: 250%;
    display: block;
    border-radius: 50%;
    background-color: DodgerBlue;
    z-index: 900;
    animation: pulse-ring 1.25s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
  }
}

@keyframes pulse-ring {
  0% { transform: scale(.33); }
  80%, 100% { opacity: 0; }
}

@keyframes pulse-dot {
  0%   { transform: scale(.5); }
  50%  { transform: scale(1); }
  100% { transform: scale(.5); }
}

@keyframes rotate-y-one-turn {
  0%   { transform: rotateY(0); }
  100% { transform: rotateY(1turn); }
}

// Tooltip classes
td {
  position: relative;
  
  #tttext {  // Chat balloon with tail
    visibility: hidden;
    width: 7rem;
    background-color: Black;
    color: White;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 120%;
    margin-left: -.5rem;
    /* Fade in ttip - takes 1 second to go from 0% to 100% opac: */
    opacity: 0;
    transition: opacity 1s;

    &::after {  // tail part
      content: "";
      position: absolute;
      top: 100%;  // at the bottom of the ttip
      left: 10%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: black transparent transparent transparent;
    }
  }

  &:hover #tttext {
    visibility: visible;
    opacity: .8;
    cursor: context-menu;
  }
}

